import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Badge, Table } from 'antd';
import TableLayout from 'Components/TableLayout'


@inject('StatisticsStore')
@observer
class StatisticsTable extends Component {
    render() {
        const { tableData, pageInfo, isLoading } = this.props.StatisticsStore
        return <div style={{ marginTop: 20 }}>
            <TableLayout size='middle' rowKey="id" loading={isLoading} onChange={this.changePage} pagination={pageInfo} columns={this.getColumns()} dataSource={tableData} />
        </div>;
    }
    changePage = (e) => {
        this.props.StatisticsStore.pageInfo = e
        this.props.StatisticsStore.pageInfo.pageIndex = e.current
        const { getData, pageInfo, columnsType, searchDate } = this.props.StatisticsStore
        getData(columnsType, { pageIndex: pageInfo.pageIndex, pageSize: pageInfo.pageSize, startDate: searchDate[0], endDate: searchDate[1] })
    }
    getColumns = () => {
        const { columnsType } = this.props.StatisticsStore
        if (columnsType === 1) {
            return [
                {
                    title: '设备名称',
                    dataIndex: 'deviceName',
                    key: 'deviceName',
                },
                {
                    title: '设备编号',
                    dataIndex: 'deviceCode',
                    key: 'deviceCode',
                },
                {
                    title: '故障次数',
                    dataIndex: 'maintainCount',
                    key: 'maintainCount',
                },
                {
                    title: '故障率',
                    dataIndex: 'maintainRate',
                    key: 'maintainRate',
                    render: (text) => (
                        <div>{text}%</div>
                    )
                },
                {
                    title: '操作',
                    align: 'right',
                    render: (text, record) => (
                        <span>
                            <a onClick={e => this.toDetail(0, record)}>详情</a>
                        </span>
                    )
                }
            ]
        } else if (columnsType === 2) {
            return [
                {
                    title: '备件名称',
                    dataIndex: 'spareName',
                    key: 'spareName',
                },
                {
                    title: '备件编号',
                    dataIndex: 'spareCode',
                    key: 'spareCode',
                },
                {
                    title: '出库',
                    dataIndex: 'used',
                    key: 'used',
                },
                {
                    title: '还库',
                    dataIndex: 'back',
                    key: 'back',
                },
                {
                    title: '库存',
                    dataIndex: 'stock',
                    key: 'stock',
                }
            ]
        } else if (columnsType === 3) {
            return [
                {
                    title: '设备名称',
                    dataIndex: 'deviceName',
                    key: 'deviceName',
                },
                {
                    title: '设备编号',
                    dataIndex: 'deviceCode',
                    key: 'deviceCode',
                },
                {
                    title: '正常点检次数',
                    dataIndex: 'checkCount',
                    key: 'checkCount',
                },
                {
                    title: '超时点检次数',
                    dataIndex: 'overtimeCheckCount',
                    key: 'overtimeCheckCount',
                },
                {
                    title: '未点检次数',
                    dataIndex: 'nocheckCount',
                    key: 'nocheckCount',
                },
                {
                    title: '操作',
                    align: 'right',
                    render: (text, record) => (
                        <span>
                            <a onClick={e => this.openLog(record)}>详情</a>
                        </span>
                    )
                }
            ]
        } else {
            return [
                {
                    title: '姓名',
                    dataIndex: 'name',
                    key: 'name',
                },
                {
                    title: '工号',
                    dataIndex: 'username',
                    key: 'username',
                },
                {
                    title: '维修次数',
                    dataIndex: 'maintainCount',
                    key: 'maintainCount',
                },
                {
                    title: '维修用时',
                    dataIndex: 'hours',
                    key: 'hours',
                },
                {
                    title: '平均用时',
                    dataIndex: 'avgHours',
                    key: 'avgHours',
                },
                {
                    title: '完成率',
                    dataIndex: 'rate',
                    key: 'rate',
                    render: (text) => (
                        <div>{text}%</div>
                    )
                },
                {
                    title: '操作',
                    align: 'right',
                    render: (text, record) => (
                        <span>
                            <a onClick={e => this.toDetail(1, record)}>详情</a>
                        </span>
                    )
                }
            ]
        }
    }
    openLog = (record) => {
        console.log(record)
        this.props.StatisticsStore.deviceId = record.deviceId
        this.props.StatisticsStore.breakenInfo = []
        this.props.StatisticsStore.getBreakenInfo({ deviceId: record.deviceId })
        this.props.StatisticsStore.modalChange()
    }
    toDetail = (type, record) => {
        this.props.StatisticsStore.detailInfo = record
        const { searchDate, getAllWorkInfo, detailInfo } = this.props.StatisticsStore
        if (type) {
            this.props.StatisticsStore.getAllWorkInfo({ userId: record.userId, startDate: searchDate[0], endDate: searchDate[1] })
            this.props.history.push('/statistics/performance')

        } else {
            this.props.history.push('/statistics/device')
        }
    }

}
export default StatisticsTable;