import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Form, Timeline, Icon, Empty, Row, Col, Button, Modal } from 'antd';
import GlobalModal from 'Components/GlobalModal';
import './index.less';

@inject('StatisticsStore')
@observer
class SparepartsDialog extends Component {
    constructor(props) {
        super(props)
        this.state = {
            val: 1,
            visible: false
        }
    }
    render() {
        const { modalVisible, breakenInfo, breakenPage, logInfo } = this.props.StatisticsStore
        // const devCurrent = {}
        return <div>{modalVisible && <GlobalModal
            title={'设备点检日志'}
            visible={modalVisible}
            onOk={e => this.props.StatisticsStore.modalChange()}
            onCancel={e => this.props.StatisticsStore.modalChange()}
            children={
                <div className='device_log' >
                    {breakenInfo.length > 0 ? (
                        <Row gutter={24}>
                            <Col span={24} className='log_col statistics_log' style={{ margin: 0, height: 600 }}>
                                <div className='ibox-title'>设备名称</div>
                                <div style={{ marginLeft: '40px' }}>
                                    <Timeline>
                                        {breakenInfo.map((item, index) => {
                                            return (
                                                <Timeline.Item
                                                    key={index}
                                                    dot={
                                                        item.result === 1 ? <Icon type="close-circle" style={{ fontSize: '16px', color: 'red' }} /> : item.result === 2 ? <Icon
                                                            type='check-circle'
                                                            style={{ fontSize: '16px', color: 'green' }}
                                                        /> : <Icon type="clock-circle" style={{ fontSize: '16px', color: 'gold' }} />
                                                    }
                                                >
                                                    <div className='time-line-content'>
                                                        <div className='right' style={{ width: '100%' }}>
                                                            <div>
                                                                <span>{item.operateUser}</span>
                                                            </div>
                                                            {item.result !== 1 ? <ul>
                                                                <li>
                                                                    <span>状态：</span>
                                                                    <span>{item.result === 2 ? '正常点检' : '超时点检'}</span>
                                                                </li>
                                                                <li>
                                                                    <span>姓名：</span>
                                                                    <span>{item.name}</span>
                                                                </li>
                                                                <li>
                                                                    <span>工号：</span>
                                                                    <span>{item.userName}</span>
                                                                </li>
                                                               {/* <li>
                                                                    <span>点检内容：</span>
                                                                    <span>{item.content}</span>
                                                                </li>
                                                                <li>
                                                                    <span>备注：</span>
                                                                    <span>{item.remark}</span>
                                                               </li>*/}
                                                                <li>
                                                                    <span>点检完成时间：</span>
                                                                    <span>{item.createTime}</span>
                                                                </li>
                                                                <li>
                                                                    <span onClick={e => this.handleOk(item)} style={{ color: '#6236FF', cursor: 'pointer' }}>查看详情</span>
                                                                </li>
                                                            </ul> : <div>
                                                                    <div>状态：超时未点检</div>
                                                                    <div>时间：{item.createTime}</div>
                                                                </div>}
                                                        </div>
                                                    </div>
                                                </Timeline.Item>
                                            );
                                        })}
                                    </Timeline>
                                    <div>
                                        {breakenPage.total === breakenInfo.length ? '已加载全部' : <Button type='primary' onClick={this.onQueryMore}>查看更多记录</Button>}
                                    </div>
                                </div>
                            </Col>
                        </Row>
                    ) : (
                            <div
                                style={{
                                    borderTop: '1px solid #ccc'
                                }}
                            >
                                <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />
                            </div>
                        )}
                </div>
            }
        />}
            <Modal
                title="详情"
                visible={this.state.visible}
                width='80%'
                onCancel={e => this.setState({
                    visible: !this.state.visible
                })}
                footer={[
                    <Button key="submit" type="primary" onClick={e => this.setState({
                        visible: !this.state.visible
                    })}>
                        确定
                    </Button>
                ]}
            >
                <div className='table-content-bk'>
                    <div className='breaken-table'>
                        {logInfo.length > 0 ? <table border="1" style={{ width: '100%' }}>
                            <tr>
                                <th>内容</th>
                                <th>结果</th>
                                <th style={{ borderBottom: 'none', borderRight: 'none' }}>详情</th>
                            </tr>
                            {!!logInfo.length && logInfo.map(item => (
                                <tr>
                                    <td className='breaken-content'>{item.content}</td>
                                    <td>
                                        <div className='breaken-result'>
                                            {item.contentType === 5 ? item.result.split(',').map(src => (src === 'undefined' ? '未上传图片' : <img src={'http://file.boranet.cn/' + src} onClick={e => window.open('http://file.boranet.cn/' + src)} style={{ width: 100, height: 100, cursor: 'pointer', marginRight: 5 }} alt='图片加载异常' />)) : item.contentType === 6 ? <audio src={'http://file.boranet.cn/' + item.result} controls="controls">
                                                您的浏览器不支持音频播放,请升级当前浏览器或换个浏览器试试吧
                            </audio> : item.result}
                                        </div>
                                    </td>
                                    <td>
                                        <div className='breaken-detail' >
                                            {item.resultVOList && item.resultVOList.map(log => (
                                                <tr style={{ height: '100%' }}>
                                                    <td >{log.content}</td>
                                                    <td className='breaken-result'>{log.contentType === 5 ? log.result.split(',').map(src => (<img src={'http://file.boranet.cn/' + src} onClick={e => window.open('http://file.boranet.cn/' + src)} style={{ width: 50, height: 50, cursor: 'pointer', marginRight: 5 }} alt='图片加载异常' />)) : log.contentType === 6 ? <audio src={'http://file.boranet.cn/' + log.result} controls="controls">
                                                        您的浏览器不支持音频播放,请升级当前浏览器或换个浏览器试试吧
    </audio> : log.result}</td>
                                                </tr>
                                            ))}
                                        </div>
                                    </td>

                                </tr>
                            ))}
                        </table> : <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />}
                    </div>
                </div>
            </Modal>
        </div>
    }
    handleOk = item => {
        if (item) {
            this.props.StatisticsStore.getByLogId({ logId: item.id })
        }
        this.setState({
            visible: !this.state.visible
        });
    };
    onQueryMore = () => {
        const { getBreakenInfo, breakenPage, deviceId } = this.props.StatisticsStore
        getBreakenInfo({ pageIndex: breakenPage.pageIndex + 1, deviceId })
    }
    componentDidMount() {

    }
}
const SparepartsDialogForm = Form.create({ name: 'define' })(SparepartsDialog);
export default SparepartsDialogForm;